@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  min-width: 100%;
}

body {
  background: rgba(0,0,0, 0.500) url('./assets/background.jpg');
  background-size: cover;
  -webkit-font-smoothing: antialiased !important;
}
html,
body,
button {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-size: 14px;
}
.container {
  margin: 80px auto;  
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.container h1 {
  color: #f05a5b;
  font-size: 36px;
}
.content{
  width: 80%;  
  opacity: 0.8;
  background: #ffffff;
  margin-top: 50px;
  border-radius: 4px;
  padding: 30px;
}

.content > p {
  font-size: 22px;
  line-height: 30px;
  margin-bottom: 30px;
}

.content button.submit-btn {
  border: 0;
  border-radius: 2px;
  width: 100%;
  height: 42px;
  padding: 0 20px;
  font-size: 16px;
  font-weight: 700;
  background: #f05a5b;  
  color: #ffffff;
  cursor: pointer;
}
.content  button.submit-btn:hover{
  background: #e14f50;
  opacity: 1;
}
.content button.secondary-btn {
  margin-top: 12px;
  border: 0;
  border-radius: 2px;
  width: 100%;
  height: 42px;
  padding: 0 20px;
  font-size: 16px;
  font-weight: 700;
  background: #337ab7;  
  color: #ffffff;
  cursor: pointer;  
}
.content  button.secondary-btn:hover{
  background: #286090;
  opacity: 1;
}

.content div.input-group{
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;

}